<template>
  <div class="main">
    
    <div class="isshowkey" v-if="begininput" @click="isShowKey"></div>
    <div class="top"></div>
    <div class="contentcar">
      <div class="cartitle">
        <ul>
          <li v-for="(item,index) in  carType"
              :class="{cartype:activeIndex==0,newcartype:activeIndex==1}"
              @click="showColor(index)">
            {{item}}
          </li>
        </ul>
      </div>
      
      <div class="inputitem" @click="showKey">
        <div v-for="(i,index) in count"
             v-bind:class="getchunkstyle(i - 1)"
             v-bind:key="i"
             @click="deleteCarNo">
          {{getLetter(i - 1) }}
          <!--{{i}}-->
        </div>
      </div>
      
      <p>请正确填写，以便我们获取信息后为您提供服务</p>
    </div>
    
    <btn @submit="submit" btnText="添加我的车牌" carNoInputBtn='true'></btn>
    
    <carnokeyboard v-on:select="selectletter"
                   v-on:delete="deleteletter"
                   v-show="begininput"
                   :carNoInputIndex="carNoInputIndex">
    </carnokeyboard>
  </div>
</template>
<script>
  
  import carnokeyboard from "./keyboard.vue";
  import btn from './BtnBox.vue'
  import {Toasts, open, close} from '../mintUi';
  import {addCarNo} from '../api/locate'
  
  export default {
    name: 'CarNoInput',
    components: {carnokeyboard, btn},
    data() {
      return {
        carType: ['普通车', '新能源车'],
        activeIndex: 0,
        begininput: false,//显示键盘
        count: 7,
        carNo: '',
        plateColor: 'BLUE',
        userId: localStorage.getItem('userId'),
        carNoList: [],
        newvalue: ''
      }
    },
    created() {
      document.title = '添加我的车牌'
      
      if (localStorage.getItem('carNo') == null) {
        
        this.carNo = ''
        
      } else {
        
        localStorage.getItem('carNo').length == 8 ? this.activeIndex = 1 : this.activeIndex = 0
        
        this.carNo = localStorage.getItem('carNo')
        
        this.begininput = false
      }
    },
    
    watch: {
      
      activeIndex: function (val) {
        
        if (val == 1) {
          
          this.count = 8;
          
          if (localStorage.getItem('carNo') != null && localStorage.getItem('carNo').length == 8) {
            
            this.carno = localStorage.getItem('carNo')
          }
        } else {
          
          this.count = 7
          
          if (localStorage.getItem('carNo') != null && localStorage.getItem('carNo').length == 7) {
            
            this.carno = localStorage.getItem('carNo')
            
          }
        }
        
      },
      carNo: function () {
        
        if ((this.count == 7 && this.carNo.length == 7) || (this.count == 8 && this.carNo.length == 8)) {
          
          this.begininput = false
          
        }
      },
    },
    
    methods: {
      
      showColor(index) {
        
        this.activeIndex = index
        
        this.carNo = (this.carNo.length == 8 && this.activeIndex == 0) ? this.carNo.substr(0, this.carNo.length - 1) : this.carNo
        
        console.log((this.carNo.length == 8 && this.activeIndex == 0) ? this.carNo.substr(0, this.carNo.length - 1) : this.carNo)
        
        console.log(this.carNo, 'hehheh')
      },
      
      showKey() {
        
        this.begininput = true
        
      },
      
      getchunkstyle(index) {
        
        if (this.count == 8) {
          
          return 'chunk greencolor'
          
        }
        else {
          
          return 'chunk noe'
        }
      },
      
      deleteCarNo() {
        
        this.begininput = true
      },
      
      
      isShowKey() {
        
        this.begininput = false
        
      },
      
      
      selectletter(value) {
        
        if ((this.count == 7 && this.carNo.length == 7) || (this.count == 8 && this.carNo.length == 8)) {
          
          return
        }
        this.carNo = this.carNo + value
        
      },
      deleteletter() {
        
        this.carNo = this.carNo.substr(0, this.carNo.length == 0 ? 0 : this.carNo.length - 1)
        
      },
      getLetter(index) {
        
        if (index >= this.carNo.length) {
          
          return ''
        }
        
        return this.carNo[index]
      },
      
      submit() {
        if ((this.carNo.length != 7 && this.activeIndex == 0) || (this.carNo.length != 8 && this.activeIndex == 1)) {
          
          Toasts('车牌格式输入有误');
          
          return
        }
        open()
        
        addCarNo({'carNo': this.carNo, 'plateColor': this.plateColor, 'userId': this.userId})   //inputcarno ？？？
          .then(data => {
            close()
            
            if (localStorage.getItem('carNoList') != null) {  //
              
              this.carNoList = localStorage.getItem('carNoList').split(',')
              
            }
            
            localStorage.setItem('carNo', this.carNo)   //将当前车牌号赋值给本地车牌号
            
            if (!this.carNoList.includes(this.carNo)) {   //如当前车牌号不再本地车牌列表中,就将他加入进去
              
              this.carNoList.push(this.carno)             //
            }
            
            localStorage.setItem('carNoList', this.carNoList)
            
            const {userId, carNo, plateColor} = data
            
            this.$router.push({
              path: 'carManagement', query: {'carNo': carNo, 'userId': userId, 'plateColor': plateColor}
            })
          })
          .catch(msg => {
            Toasts(msg)
            close()
          })
        
      },
    },
    
    computed: {
      
      carNoInputIndex() {
        
        if (this.carNo.length == 0) {
          
          return 0
          
        } else if (this.carNo.length == 1) {
          
          return 1
        }
        if ((this.activeIndex == 0 && this.carNo.length == 6) || (this.activeIndex == 1 && this.carNo.length == 7)) {
          
          return 3
        }
        return 2
      },
    },
  }

</script>

<style scoped lang="less">
  
  * {
    padding: 0;
    margin: 0;
    list-style: none;
    font-size: 10px;
  }
  
  .isshowkey {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0);
    z-index: 3;
  }
  
  .main {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: #F0EFF6;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .header1 {
    height: 6.4rem;
    width: 100%;
    background: #353639;
    
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .header1 p {
    margin-top: 2.8rem;
    font-family: PingFangSC-Regular;
    font-size: 1.8rem;
    color: #FFFFFF;
  }
  
  .top {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 23.9rem;
    background: #64C6E7;
    z-index: -1;
  }
  
  .contentcar {
    margin-top: 13.9rem;
    width: 92%;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    align-items: center;
    -webkit-flex-direction: column;
    box-sizing: border-box;
    padding: 2.5rem 0;
    background: rgba(255, 255, 255, 0.90);
    box-shadow: 0 2px 14px 0 rgba(39, 52, 125, 0.10);
    border-radius: 0.5rem;
  }
  
  .cartitle {
    margin: 0rem 0 2rem 0;
    text-align: center;
    border-radius: 0.2rem;
    height: 3.5rem;
  }
  
  .cartitle ul {
    width: 18rem;
    display: flex;
    display: -webkit-flex;
    justify-content: space-around;
    -webkit-justify-content: space-around;
    margin: 0 auto;
  }
  
  .cartitle ul li {
    box-sizing: border-box;
    width: 9rem;
    line-height: 3.5rem;
    text-align: center;
    font-size: 1.2rem;
  }
  
  .cartype:first-child { /*车型的css格式*/
    background: #1C75BC;
    border: 0.05rem solid #1C75BC;
    border-radius: 0.5rem 0 0 0.5rem;
    color: #fff;
  }
  
  .cartype:last-child {
    border: 0.05rem solid #1C75BC;
    border-radius: 0 0.5rem 0.5rem 0;
    color: #1C75BC;
  }
  
  .newcartype:first-child {
    background: #fff;
    border: 0.05rem solid #63C8A9;
    border-radius: 0.5rem 0 0 0.5rem;
    color: #63C8A9;
  }
  
  .newcartype:last-child {
    background: #63C8A9;
    border: 0.05rem solid #63C8A9;
    border-radius: 0 0.5rem 0.5rem 0;
    color: #fff;
  }
  
  .inputitem {
    box-sizing: border-box;
    padding: 0 1.5rem;
    box-sizing: border-box;
    height: 4.3rem;
    width: 100%;
    display: flex;
    display: -webkit-flex;
  }
  
  .chunk {
    width: 3.5rem;
    border: 0.05rem solid #646464;
    border-left: 0px;
    height: 4.2rem;
    flex-grow: 1;
    flex-shrink: 1;
    text-align: center;
    color: #1b1c1c;
    line-height: 4.2rem;
    font-size: 1.8rem;
    font-family: "Microsoft Yahei", "Arial", "Helvetica";
  }
  
  .noe:first-child {
    border: 0.05rem solid #646464;
  }
  
  .greencolor {
    color: #63C8A9;
    border: 0.05rem solid #63C8A9;
  }
  
  .greencolor:not(:last-child) {
    border-right: none;
  }
  
  .contentcar p:last-child {
    margin-top: 3.15rem;
    font-family: PingFangSC-Regular;
    font-size: 1.2rem;
    color: #9B9B9B;
    letter-spacing: 0;
    text-align: center;
  }

</style>



